.contact__container {
  background-color: #f5f5f5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 100px;
}

.contact__pane-contact {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 50%;
  flex: 1 1 50%;
  color: #333;
}

.contact__pane-form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 50%;
  flex: 1 1 50%;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 1vh 1vw;
}

.form__input {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 45%;
  flex: 1 1 45%;
  min-width: 200px;
  padding: 5px;
  margin: 0px 6px 3vh 0px;
  min-height: 50px;
  color: #333;
  border: none;
}

@media (max-width: 768px) {
  .contact__container {
    padding: 100px 35px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }

  .contact__pane-form {
    -webkit-box-ordinal-group: 0;
    -ms-flex-order: -1;
    order: -1;
    padding: 0;
    margin-bottom: 7vh;
  }

  .form__input {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 100%;
    flex: 1 1 100%;
    min-width: 150px;
  }
}
